document.addEventListener('DOMContentLoaded', function(){
    eventListeners();
});

function eventListeners(){
    const menuMobile = document.querySelector('.mobile-menu');
    menuMobile.addEventListener('click',navegacionResponsive)

    const btnCambiarEstado = document.querySelectorAll('.cambiarEstado');

    const tipoPedido = document.querySelector('#tipo');
    tipoPedido.addEventListener('blur',bloquearDestinos);

    btnCambiarEstado.forEach(pedidos =>{
        pedidos.addEventListener('click',cambiarEstado);
    });

    const inputBuscador = document.querySelectorAll('.buscador');
    inputBuscador.forEach(input =>{

        input.addEventListener('input',filtrarPedidos);

    });

};

function bloquearDestinos(e){
    
    const selectDestino = document.querySelector('#destino').options;
    console.log(selectDestino[1]);
    if(e.target.value === '2'){
        for(let i = 0; i< selectDestino.length ; i++){
            if(selectDestino[i].value ==='12'){
                selectDestino[i].setAttribute('selected',"");
            } else {
                selectDestino[i].setAttribute('disabled',"");
            }
        }
   } else {
        for(let i = 0; i< selectDestino.length ; i++){
            if(selectDestino[i].value ==='12'){
                selectDestino[i].setAttribute('disabled',"");
                selectDestino[i].removeAttribute('selected',"");
            } else {
                selectDestino[i].removeAttribute('disabled',"");
                selectDestino[i].removeAttribute('selected',"");
            }
        }
   }

}

function navegacionResponsive(){
    const navegacion = document.querySelector('.navegacion');
    navegacion.classList.toggle('mostrar');
};

function cambiarEstado(e){
    e.preventDefault();

    const sucursalUsuario = document.querySelector('#user').value;
    // obtenemos el id del pedido y la acciÃ³n.
    const enlace = e.target;
    const idEnlace = e.target.id;
    const accion = idEnlace.split("-")[0];
    const id = idEnlace.split("-")[1];
    const tdTipo = e.target.parentElement.parentElement.parentElement.childNodes[3];
    const tipo = tdTipo.textContent;
    const tdOrigen = e.target.parentElement.parentElement.parentElement.childNodes[7];
    const tdDestino = e.target.parentElement.parentElement.parentElement.childNodes[11];
    const tdOperador = e.target.parentElement.parentElement.parentElement.childNodes[15];
    const tdStatus = e.target.parentElement.parentElement.parentElement.childNodes[17];
    
    // reviso si el pedido a despachar, tiene operador asignado
    const operadorId = e.target.parentElement.parentElement.parentElement.childNodes[15].id;

    // reutilizar agregando que cuando sea recibido y no es el usuario el de destino.
    if(operadorId === '1'){
        // mostramos el selector de operadores
        const emergente = document.querySelector('#emergente');
        emergente.classList.remove('ocultar');

        // registramos el evento del click en el boton y ejecutamos el ajax
        const botonEmergente = document.querySelector('#botonEmergente');
        botonEmergente.addEventListener('click',function (){
            const origen = tdOrigen.id.split("-")[1];
            // revisar si estÃ¡ No Asignado y el origen no coincide con el usuario que estÃ¡ ingresando;
            if(sucursalUsuario !== origen){
                alert("no puede despachar");
            } else {
                let operadorElegido = document.querySelector('#operador').value;
                let nombreOperador = document.querySelector('#operador').options[document.querySelector('#operador').options.selectedIndex].textContent;
                    // ajax para cambiar el estado
                let xhr = new XMLHttpRequest();
                
                xhr.open('GET',`../admin/propiedades/actualizarEstado?id=${id}&nombreOperador=${nombreOperador}&operador=${operadorElegido}&accion=${accion}`,true);
            
                xhr.onload = function(){
            
                    if(xhr.status===200){
                        let respuesta = JSON.parse(xhr.responseText);
                        
                        if(respuesta.resultado === 'Exito'){
                            
                            tdOperador.children[0].innerHTML=nombreOperador;
                            if(tipo === 'ED'){
                                tdStatus.innerHTML='En Camino';
                                enlace.textContent = 'Confirmar Entrega';
                                enlace.classList.add('gris');
                                enlace.classList.remove('amarillo');
                                enlace.id = `Confirmar-${id}`;
                            } else {
                                tdStatus.innerHTML='En Camino';
                                enlace.textContent = 'Recibir';
                                enlace.classList.add('verde');
                                enlace.classList.remove('amarillo');
                                enlace.id = `Recibir-${id}`;
                            }
                            
                            
                        }
                    }    
                };
        
                xhr.send();
                            
                setTimeout(() => {
                    emergente.classList.add('ocultar');        
                }, 700);                
            }
        });
    }
    
    if(accion === 'Recibir'){

        let destino = tdDestino.id.split("-")[0];
        let destinoId = tdDestino.id.split("-")[1];
        
        // ajax para cambiar el estado
        let xhr = new XMLHttpRequest();
    
        xhr.open('GET',`../admin/propiedades/actualizarEstado?id=${id}&tipo=${tipo}&accion=${accion}&destinoId=${destinoId}`,true);
        xhr.onload = function(){
            console.log(xhr.responseText);
            if(xhr.status===200){
                
                let respuesta = JSON.parse(xhr.responseText);
                
                if(respuesta.resultado === 'Exito'){
                    
                    if(tipo === 'PM'){
                        tdStatus.innerHTML='Recibido';    
                        enlace.textContent = 'Archivar';
                        enlace.classList.remove('verde');
                        enlace.classList.add('celeste');
                        enlace.id = `Archivar-${id}`;
                    } else if(tipo === 'RS'){
                        tdStatus.innerHTML='Recibido';    
                        enlace.textContent = 'Entregar';
                        enlace.classList.remove('verde');
                        enlace.classList.add('rosa');
                        enlace.id = `Entregar-${id}`;
                    } else {    
                        enlace.textContent = 'Confirmar Entrega';
                        enlace.classList.remove('verde');
                        enlace.classList.add('gris');
                        enlace.id = `Confirmar-${id}`;
                    }
                }
            }
    
        };
    
        xhr.send();

            setTimeout(() => {
                emergente.classList.add('ocultar');        
            }, 700);
        

    }
}

function filtrarPedidos(e){

    let nodo;
    switch(e.target.id){

        case 'buscarPedido': 
            nodo=5;
            break;
        case 'buscarOrigen':
            nodo=7;
            break;
        case 'buscarDestino':
            nodo=11;
            break;
        case 'buscarOperador':
            nodo=15;
            break;
        case 'buscarStatus':
            nodo=17;
            break;
        default: 
            break;
    }

    const expresion = new RegExp(e.target.value, "i" );

    const trPedidos = document.querySelectorAll('.propiedades tbody tr');   

    trPedidos.forEach(tr =>{
        tr.style.display = 'none';
        
        if(tr.childNodes[nodo].textContent.replace(/\s/g, " ").search(expresion) != -1 ){
            tr.style.display = 'table-row';
        }
    });

}